import React, { Component } from 'react';
import Note from './Note';
import SearchNotes from './SearchNotes';
import Loading from './Loading';

class ListNotes extends Component {
    constructor() {
        super();

        this.state = {
            showDeleteBtn: false
        }

        this.toggleDeleteBtn = this.toggleDeleteBtn.bind(this);
    }

    toggleDeleteBtn() {
        this.setState({
            showDeleteBtn: !this.state.showDeleteBtn
        })
    }

    render() {
        if (this.props.isLoading) {
            return <Loading />
        } else {
            return(
                <article className="list-appointments-layout py-3 mb-5">
                    <div className="d-flex justify-content-end mr-5">
                        <button className={"btn btn-info btn-lg " + (this.props.listDisplay ? 'hidden' : '')} onClick={this.props.toggleList}>
                            <h6>See Notes <i className="far fa-address-book"></i></h6>
                        </button>
                    </div>
                    
                    <section className={"container card-header " + (this.props.listDisplay ? '' : 'hidden')}>
                        <div className="d-flex bg-secondary">
                            <div className="search-component col-6">
                                <SearchNotes searchNotes={this.props.searchNotes} />
                            </div>
                            <button className="btn btn-info btn-md ml-auto my-2 mr-2" onClick={this.props.fetchNotes}>
                                <i className="fas fa-sync"></i>
                            </button>
                            <button className="btn btn-warning btn-sm my-2 mr-2" onClick={this.toggleDeleteBtn}>
                                <h5 className="text-white"><i className="fas fa-user-times"></i></h5>
                            </button>
                            <button className={"btn btn-danger my-2 mr-2 "} onClick={this.props.toggleList}><i className="fas fa-times"></i></button>
                        </div>
    
                        <section className="note-list row overflow-auto bg-light card-body">
                            {
                                this.props.notes.map(note => (
                                    <Note 
                                        note={note} 
                                        showDeleteBtn={this.state.showDeleteBtn} 
                                        deleteNote={this.props.deleteNote}
                                        editNote={this.props.editNote}
                                        key={note.noteId}
                                        />
                                ))
                            } 
                            
                        </section>
                    </section>
                </article>
            )
        }
        
    }
}

export default ListNotes;