import './App.css';
import React, { Component } from 'react';
import NewNote from './components/NewNote';
import ListNotes from './components/ListNotes';
import LogIn from './components/LogIn';
import LogOut from './components/LogOut';
import { withAuth0 } from '@auth0/auth0-react';
import Loading from './components/Loading';

class App extends Component {
  constructor() {
    super();
    this.state = {
      notes: [],
      lastIndex: 0,
      formDisplay: true,
      aptBtnDisplay: false,
      listDisplay: true,
      queryText: '',
      isLoading: true
    }

    this.fetchNotes = this.fetchNotes.bind(this);
    this.toggleDisplay = this.toggleDisplay.bind(this);
    this.addNote = this.addNote.bind(this);
    this.toggleList = this.toggleList.bind(this);
    this.searchNotes = this.searchNotes.bind(this);
    this.deleteNote = this.deleteNote.bind(this);
    this.editNote = this.editNote.bind(this);
  }

  fetchNotes() {
    this.setState({
      isLoading: true
    });

    fetch('https://morning-anchorage-80357.herokuapp.com/https://note-taker-api.glitch.me/notes').then(response => {
      response.json().then(data => {
        let index = 0;
        const temp_notes = data.map(note => {
          note.noteId = index;
          index += 1;
          return note;
        })

        this.setState({ notes: temp_notes, lastIndex: index, isLoading: false });
        console.log(this.state);
      })
    })
  }

  componentDidMount() {
    this.fetchNotes();
  }

  toggleDisplay() {
    this.setState({
      formDisplay: !this.state.formDisplay,
      aptBtnDisplay: !this.state.aptBtnDisplay
    })
  }

  addNote(note) {
    note.noteId = this.state.lastIndex;
    let temp_notes = this.state.notes;
    temp_notes.unshift(note);

    this.setState({
      notes: temp_notes,
      lastIndex: this.state.lastIndex + 1
    })

    
    fetch('https://morning-anchorage-80357.herokuapp.com/https://note-taker-api.glitch.me/new-note', {
      method: "POST",
      body: JSON.stringify(note),
      headers: {"Content-type": "application/json; charset=UTF-8"}
    })
  }

  deleteNote(note) {
    let url = 'https://morning-anchorage-80357.herokuapp.com/https://note-taker-api.glitch.me/delete-note?id=';
    url = url.concat(note.orderNumber);
  
    fetch(url, {
      method: "POST"
    }).then(response => {
      console.log(response);
    })
    this.fetchNotes();
  }

  editNote(note) {
    let url = 'https://morning-anchorage-80357.herokuapp.com/https://note-taker-api.glitch.me/edit-note?id=';
    url = url.concat(note.orderNumber);
    console.log(note);
    fetch(url, {
      method: "POST",
      body: JSON.stringify(note),
      headers: {"Content-type": "application/json; charset=UTF-8"}
    }).then(response => {
      console.log(response);
    })

    this.fetchNotes();
  }

  toggleList() {
    this.setState({
      listDisplay: !this.state.listDisplay
    })
  }

  searchNotes(text){
    this.setState({
      queryText: text
    })
  }

  render() {
    const { isAuthenticated, isLoading } = this.props.auth0;

    if(isLoading) {
      return <Loading />
    }

    if (!isAuthenticated) {
      return (
        <LogIn />
      )
    }

    // Filter notes using the search query
    let filteredNotes = this.state.notes;
    filteredNotes = filteredNotes.filter(note => {
      return(
        note['customerName'].toLowerCase().includes(this.state.queryText.toLowerCase()) ||
        note['orderNumber'].toLowerCase().includes(this.state.queryText.toLowerCase()) ||
        note['issue'].toLowerCase().includes(this.state.queryText.toLowerCase()) ||
        note['stepsTaken'].toLowerCase().includes(this.state.queryText.toLowerCase()) ||
        note['date'].toLowerCase().includes(this.state.queryText.toLowerCase())
      )
    })

    // Sort notes by noteId (to display the newest at the top)
    filteredNotes.sort((a, b) => {
      return b.noteId - a.noteId; 
    })

    return (
      <main className="container mt-5">
        {isAuthenticated && <LogOut />}
        <NewNote 
          formDisplay={this.state.formDisplay}
          aptBtnDisplay={this.state.aptBtnDisplay}
          toggleDisplay={this.toggleDisplay}
          addNote={this.addNote}
        />
        <ListNotes
          notes={filteredNotes}
          listDisplay={this.state.listDisplay}
          toggleList={this.toggleList}
          searchNotes={this.searchNotes}
          deleteNote={this.deleteNote}
          isLoading={this.state.isLoading}
          editNote={this.editNote}
          fetchNotes={this.fetchNotes}
        />
      </main>
    );
  }
}

export default withAuth0(App);
