import React, {Component} from 'react';

class NewNote extends Component {
    constructor() {
        super();
        this.state = {
            customerName: '',
            orderNumber: '',
            issue: '',
            stepsTaken: '',
            date: ''
        }

        this.handleChanges = this.handleChanges.bind(this);
        this.saveNote = this.saveNote.bind(this);
    }

    handleChanges(event) {
        const target = event.target;
        const value = target.value;
        const name = target.name;
    
        this.setState({[name]: value})
    }

    saveNote(event) {
        event.preventDefault();

        var date = new Date();
        let current_time = (date.getMonth() + 1) + '-' + date.getDate() + '-' + date.getFullYear();
        console.log(current_time);
        let new_note = {
            customerName: this.state.customerName,
            orderNumber: this.state.orderNumber,
            issue: this.state.issue,
            stepsTaken: this.state.stepsTaken,
            date: current_time
        }

        console.log(new_note);

        this.props.addNote(new_note);

        this.setState({
            customerName: '',
            orderNumber: '',
            issue: '',
            stepsTaken: '',
            date: ''
        })

        this.props.toggleDisplay();
    }

    render() {
        return (<article className="">
            <div className="d-flex justify-content-end mr-5">
                <button className={
                        "layout-dropdown btn btn-success btn-lg " + (
                        this.props.aptBtnDisplay ? '' : 'hidden'
                    )
                    }
                    onClick={
                        this.props.toggleDisplay
                }>
                    <h5>
                        New Note <i className="fas fa-plus-circle ml-1"></i></h5>
                </button>
            </div>

            <div className={
                "container " + (
                this.props.formDisplay ? '' : 'hidden'
            )
            }>
                <div className="row justify-content-center ">
                    <section className="col-sm-12 col-md-10 new-note-layout">
                        <div className="card-header text-center">
                            <h5>New Note</h5>
                        </div>
                        <div className="card-body">

                            <form className="appointment-form" noValidate
                                onSubmit={
                                    this.saveNote
                            }>
                                <div className="form-group form-row">
                                    <label className="col-form-label">Customer Name:</label>
                                    <div className="col-md-3 col-sm-5">
                                        <input onChange={
                                                this.handleChanges
                                            }
                                            type="text"
                                            className="form-control"
                                            name="customerName"
                                            id="customerName"></input>
                                    </div>
                                </div>

                                <div className="form-group form-row">
                                    <label className="col-form-label">Order number:</label>
                                    <div className="col-md-3 col-sm-5">
                                        <input onChange={
                                                this.handleChanges
                                            }
                                            type="text"
                                            className="form-control"
                                            name="orderNumber"
                                            id="orderNumber"></input>
                                    </div>
                                </div>

                                <div className="form-group form-row">
                                    <label className="col-form-label">Issue:
                                    </label>
                                    <div>
                                        <textarea onChange={
                                                this.handleChanges
                                            }
                                            className="form-control"
                                            rows="4"
                                            cols="70"
                                            name="issue"
                                            id="issue"></textarea>
                                    </div>
                                </div>

                                <div className="form-group form-row">
                                    <label className="col-form-label">Steps Taken:
                                    </label>
                                    <div>
                                        <textarea onChange={
                                                this.handleChanges
                                            }
                                            className="form-control"
                                            rows="4"
                                            cols="70"
                                            name="stepsTaken"
                                            id="stepsTaken"></textarea>
                                    </div>
                                </div>

                                <div className="form-group form-row justify-content-center">
                                    <button className="btn btn-danger mx-3"
                                        onClick={
                                            this.props.toggleDisplay
                                        }
                                        type="reset">Cancel</button>
                                    <button className="btn btn-success" type="submit">Save</button>
                                </div>

                            </form>

                        </div>
                    </section>
                </div>
            </div>
        </article>)

    }
}

export default NewNote;
