import React, { Component } from 'react';

class Note extends Component {
    constructor() {
        super();

        this.state = {
            isEditing: false,
            editableNote: {}
        }

        this.toggleisEditing = this.toggleisEditing.bind(this);
        this.handleChanges = this.handleChanges.bind(this);
        this.cancelChanges = this.cancelChanges.bind(this);
        this.saveChanges = this.saveChanges.bind(this);
    }

    toggleisEditing() {
        this.setState({
            isEditing: !this.state.isEditing
        })
    }

    handleChanges(event) {
        let target = event.target;
        let value = target.value;
        let name = target.name;

        let new_note = this.state.editableNote;
        new_note[name] = value;
        
        this.setState({
            editableNote: new_note
        })
    }

    copyText(id) {
        let element = document.getElementById("steps-" + id);
        element.select();
        element.setSelectionRange(0, 99999)
        document.execCommand("copy");
    }

    cancelChanges() {
        this.setState({
            editableNote: this.props.note
        })
        console.log('Canceled changes');
        console.log(this.props.note);
        this.toggleisEditing();
        
    }

    saveChanges() {
        console.log('changes saved');
        this.toggleisEditing();
        this.props.editNote(this.state.editableNote);
    }

    componentDidMount() {
        let noteData = this.props.note;
        this.setState({
            editableNote: noteData
        })
    }

    render() {
        return(
            <div className="note-layout row py-3 col-12">
                <div className="col-12">
                    <div className="note-head d-flex mb-3">
                        <h6 className="customer-name"> 
                            {this.props.note.customerName}
                        </h6>
                        <span className="note-date ml-auto font-italic">
                            {this.props.note.date}
                        </span>
                        <button className={"btn btn-danger btn-small ml-2 " + (this.props.showDeleteBtn ? '' :'hidden')} onClick={e => this.props.deleteNote(this.props.note)} >
                            <i className="fas fa-trash-alt"></i>
                        </button> 
                    </div>
                    
                    <div className="order-number d-flex mb-3">
                        <span className="label-item text-secondary">Order number:  </span>
                        <span>{this.props.note.orderNumber}</span>
                        <i className="note-edit-button fas fa-pencil-alt fa-md mt-auto ml-auto"
                            onClick={this.toggleisEditing}
                            ></i>
                    </div>

                    <div className="order-issue d-flex mb-3">
                        <span className="label-item text-secondary">Issue:  </span>
                        <textarea 
                            type="text"
                            name="issue"
                            value={this.state.editableNote.issue} 
                            className="form-control w-50 issue-note" 
                            cols="70" 
                            rows="6"
                            readOnly={!this.state.isEditing}
                            onChange={this.handleChanges}
                            />
                    </div>

                    <div className="order-steps d-flex mb-3">
                        <span className="label-item text-secondary">Steps Taken:  </span>
                        <textarea 
                            id={"steps-" + this.props.note.orderNumber} 
                            name="stepsTaken"
                            type="text" value={this.state.editableNote.stepsTaken} 
                            className="form-control w-50 stepsTaken-note" 
                            cols="70"
                            rows="6"
                            onChange={this.handleChanges}
                            readOnly={!this.state.isEditing}/>
                        <i onClick={e => this.copyText(this.props.note.orderNumber)} className="clipboard-button far fa-copy ml-auto fa-lg "></i>
                    </div>

                    <div className={"edit-actions text-center " + (this.state.isEditing ? '' : ' hidden')}>
                        <button className="btn btn-danger mx-3" onClick={this.cancelChanges}>Cancel</button>
                        <button className="btn btn-success" onClick={this.saveChanges}>Save</button>
                    </div>                
                </div>
            </div>
        )
    }
}

export default Note;